@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;600;800&display=swap);
body{
  background-color: #F0F0F2;
}
.cardBackground{
    padding: 30px;
    background-color: #FDFDFF;
    margin: 20px;
    border-radius: 20px
}

.cardhead{
    font-family: 'Poppins', sans-serif;
    font-family: 800;
}

.cardContent{
    font-family: 'Poppins',sans-serif;
    font-weight: 300;
}


.img {
    border-radius: 20px;
    margin-left: 45px;
    width: 450px;
}

.recipe{
    background-color: #FDFDFF;
    border-radius: 20px;
    padding-right: 20px;
}

@media only screen and (max-width:600px) {
    .img{
        width: 305px;
    }
}
